.card-container {
    /* background-color: aqua; */
    display: flex;
    margin: auto;
    margin-top: 5rem;
    margin-bottom: 5rem;
    width: 75vw;
    justify-content: center;
    align-items: center;
}

.card-wapper {
    margin: 0 2rem !important;
}

.card__infected {
    border-bottom: 5px solid rgb(77, 77, 235);
}

.card__recovered {
    border-bottom: 5px solid rgb(114, 235, 77);
    margin: 0 1rem;
}

.card__deaths {
    border-bottom: 5px solid rgb(235, 77, 77);
    margin: 0 1rem;
}